@import "../../App.scss";

.services {
    background: linear-gradient(180deg, #06060f #171746, );
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;


    .textContainer {
        flex: 1;
        align-self: flex-end;
        display: flex;
        align-items: center;
        gap: 20px;

        @include mobile {
           align-self: center;
           text-align: center;
           flex-direction: column    ;
            } 


        p {
            font-weight: 200;
            font-size: 20px;
            color: gray;
            text-align: right;
        }

        hr {
            width: 500px;
            border: none;
            border-top: 0.5px solid gray;
        }
    }

    .titleContainer {
        flex: 2;
        display: flex;
        flex-direction: column;
        align-items: center;


        .title {
            display: flex;
            align-items: center;
            gap: 50px;


            img {
                width: 300px;
                height: 100px;
                border-radius: 50px;
                object-fit: cover;
            }

            h1 {
                font-weight: 100;
                font-size: 96px;
            }


            button {
                width: 300px;
                height: 100px;
                border-radius: 50px;
                background-color: orange;
                border: none;
                font-size: 24px;
                cursor: pointer;
            }
        }
    }

    .listContainer {
        flex: 2;
        display: flex;
        max-width: 1200px;
        margin: auto;


        .box {
            padding: 30px;
            border: 0.5px solid gray;
            display: flex;
            flex-direction: column;
            justify-content: space-between;


            button {
                padding: 10px;
                background-color: orange;
                border: none;
                cursor: pointer;
            }
        }
    }
}